  border: 1px solid #eee;
  border-radius: 4px;
  width: 70%;
  height: 1000px;
  color: #0000008A;
  height: 30px;
  background-color: white;
}

#gmail svg {
  float: left;
}

#gmail div {
  float: left;
  padding-left: 10px;
}

#gg {
  padding-left: 5px;
}

#img {
  background: cover;
}

#book {
  margin-top: 40%;
  margin-left: 9%;
  width: auto;
  max-width: 249px;
  height: 242px;
  max-height: auto;
}

