.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  clear: both;
  z-index: 3;
  bottom: 0;
}

#modal-main {
  position:fixed;
  background: white;
  top:53%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  clear: both;
  margin-bottom: 0;
}

.display-block {
  display: block;
  clear: both;
}

.display-none {
  display: none;
  clear: both;
}

#close{
  margin: 20px 20px 0px 20px;
  float: right;
}

